export default {
	data() {
		return {
			isOpen:true,
			bgMp3Url:"",
			number:1,
			time:''
		}
	},
	onLoad(option) {
		
		// 判断手机型号
		// this.$store.commit('platform')
		// const i = this.$store.state.platform
		this.$store.commit('stop')
			
		
		// // 测试
		// option.num = 6
		
		// 使手机振动15ms
		wx.vibrateShort()
		uni.stopAccelerometer()
		
		// 接受变量参数
		if(option.num){
			this.$store.commit('setNum',option.num)
		}else{
			this.$store.commit('randomNum')
		}
		this.number = this.$store.state.num		
		
		clearTimeout(this.time)
		this.time = setTimeout(() => {
			this.openClick()
		},9000)
		
	},
	onShow() {
		if(!this.$store.state.bgAudio.paused){
			this.$store.state.bgAudio.pause()
		}
		if(this.$store.state.isRandom){
			this.$store.commit('randomNum')
		}
		if(this.$store.state.audioCtx.paused){
			this.$store.commit('play',{
				url:'/static/27.mp3',
				loop:true	
			})
		}
		
		
		// 测试部分
		this.$store.commit('setNum',6)
		this.number = 6
	},
	onHide(){
		this.$store.commit('stop')
	},
	methods: {
		openClick(){
			clearTimeout(this.time)
			this.$store.commit('stop')
			if(this.number <= 5){
				uni.navigateTo({
					url:"../detail/detail",
				})
			}
			else{
				uni.navigateTo({
					url:"../shake/shake",
				})
			}
			
		},
		refuse(){
			this.isOpen = this.isOpen
		},
		mytouchmove(){
			// 使手机振动15ms
			wx.vibrateShort()
		}
	}
	
}
